import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import updateoulista from '@/components/updateoulista'
import lista from '@/components/lista'
import novo from '@/components/novo'
import verExame from '@/components/verExame'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/updateoulista/:Pid',
      name: 'updateoulista',
      component: updateoulista
    },
    {
      path: '/lista/:Pid',
      name: 'lista',
      component: lista
    },
    {
      path: '/novo/:Pid',
      name: 'novo',
      component: novo
    },
    {
      path: '/verExame/:Pid',
      name: 'verExame',
      component: verExame
    }
  ]
})