<template>
    <div class = "novo">
        <v-toolbar
            color="blue darken-3"
            dark
            class="headline"
        >
            <v-btn 
                large
                icon
                @click="back"
                light
            >
                <v-avatar size="35px" tile>
                    <img
                    src="../components/logo2.png"
                    alt="Health Cloud"
                    >
                </v-avatar>
            </v-btn>

            <v-toolbar-title class="white--text">Novo Exame</v-toolbar-title>

            <v-spacer></v-spacer>

            <v-icon large>local_hospital</v-icon>
        </v-toolbar>

        <v-card
            align="center" 
            justify="center" 
            max-width="600" 
            class="headline mx-auto"
        >
            <v-card-title>
                Formulário
            </v-card-title>
            <v-card flat>
                <input type="file" id="file" ref="image" accept="image/*" v-on:change="handleFileUpload()"/>
            </v-card>
            <v-form
                ref="form"
                lazy-validation
                max-width="400"
            >
                <v-text-field
                v-model="name"
                label="Nome do Paciente"
                required
                ></v-text-field>

                <v-text-field
                v-model="cpf"
                label="CPF do Paciente"
                required
                ></v-text-field>

                <v-text-field
                v-model="crm"
                label="CRM do Médico"
                required
                ></v-text-field>
            
                <v-checkbox
                v-model="masc"
                label="Masculino"
                required
                ></v-checkbox>

                <v-checkbox
                v-model="fem"
                label="Feminino"
                required
                ></v-checkbox>

                <v-date-picker no-title dark></v-date-picker>

            </v-form>

            <v-btn
                :loading="loading1"
                :disabled="loading1"
                color="blue darken-4"
                class="white--text"
                @click="loader = 'loading1'"
            >   
                Submeter
                <v-icon right dark>get_app</v-icon>
            </v-btn>
        </v-card>
    </div>
</template>

<script>
    import { saveAs } from 'file-saver';

    export default{
            data (){
                return{
                    file: '',
                    name: '',
                    cpf: '',
                    masc: false,
                    fem: false,
                    loading1: false,
                    loader: null,
                }
            },
            methods: {
                back: function() {
                    this.$router.push({ name: 'login'})
                },
                handleFileUpload(){
                    this.file = this.$refs.file.files[0];
                },
                submitFile(){
                    var FileSaver = require('file-saver');
                    var blob = new Blob([this.file+"\n"+this.name+"\n"+this.cpf+"\n"+this.masc+"\n"+this.fem], {type: "text/plain;charset=utf-8"});
                    FileSaver.saveAs(blob, this.crm+"/"+this.cpf+".txt"); 
                }
            },
            watch: {
                loader () { 
                    const l = this.loader
                    this[l] = !this[l]
                    
                    setTimeout(() => (
                        this[l] = false,
                        location.reload()
                    ), 3000)
                    this.loader = null
                }
            }
    }
</script>