<template>
  <v-card align="center" justify="center" max-width="400" class="mx-auto headline">
    <img src="../components/logo.png" class = "center">

    <v-text-field
      label="Identificação"
      single-line
      outlined
      v-model="login"
    ></v-text-field>

    <v-text-field
      label="Senha"
      single-line
      outlined
      v-model="senha"
      :type="show1 ? 'text' : 'password'"
    ></v-text-field>
    <v-btn 
      depressed small
      @click="logar"
    >
      Entrar
    </v-btn>
  </v-card>
</template>

<script>
  export default{
    name: 'login',
    data () {
      return {
        title:'Login',
        login: '',
        senha: ''
      }
    },
    methods: {
      logar: function(){
        if(this.login == '12345' && this.senha=='12345'){
          this.$router.push({ name: 'novo',params:{Pid:this.login} })
        }
        else if(this.login == '2345' && this.senha=='12345'){
          this.$router.push({ name: 'lista',params:{Pid:this.login} })
        }
        else{
          alert('Login Inválido');
        }
      }
    }
  }
</script>
